html {
  scroll-behavior: smooth;
}

body {
  background-color: #131313;
}

@font-face {
  font-family: Metropolis-Black;
  src: url(fonts/Metropolis-Black.ttf);
}

@font-face {
  font-family: Metropolis-Light;
  src: url(fonts/Metropolis-Light.ttf);
}

@font-face {
  font-family: Metropolis-Medium;
  src: url(fonts/Metropolis-Medium.ttf);
}

@font-face {
  font-family: Metropolis-Regular;
  src: url(fonts/Metropolis-Regular.ttf);
}

.hero-container {
  max-width: 100%;
  height: 100vh;
  overflow: hidden;
  position: relative;
}

.hero-container .video-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #394d557c;
  mix-blend-mode: overlay;
}

.hero-container video {
  position: absolute;
  top: 0;
  left: 0;
  -o-object-fit: cover;
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.hero-container .title {
  text-align: center;
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  z-index: 1;
  color: #ececec;
  top: 20%;
}

.hero-container .title .logotype {
  font-family: "Metropolis-Regular";
  font-size: 11.089em;
  letter-spacing: 0.03em;
  margin: 0;
  line-height: 1.2;
}

.hero-container .title .slogan {
  font-family: "Inter", sans-serif;
  font-size: 1.618rem;
  font-weight: 300;
  letter-spacing: 0.4px;
  margin: 2em 0;
}

.hero-container .title button.cta {
  font-family: "Inter", sans-serif;
  font-size: 1.618em;
  text-transform: uppercase;
  border: 0;
  border-radius: 12px;
  padding: 30px 80px;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 1px 2px 0px;
  color: #fff;
  background: #171717;
}

.hero-container .title button.cta:hover {
  color: #171717;
  background-color: #fff;
  text-decoration: underline;
  text-underline-offset: 5px;
  transition: 0.4s ease-in-out;
}

.testimonial {
  height: 30vh;
  max-width: 100vw;
  color: #fff;
  font-size: 1.125em;
}

.testimonial .testimonial-container {
  background: -webkit-gradient(
      linear,
      left bottom,
      left top,
      from(rgba(61, 113, 172, 0.3)),
      color-stop(73%, rgba(93, 133, 199, 0.3)),
      to(rgba(126, 156, 189, 0.3))
    ),
    url(img/Aleksander-Victorian-City-hero-shot.jpg) no-repeat;
  background: linear-gradient(
      0deg,
      rgba(61, 113, 172, 0.35) 0%,
      rgba(93, 134, 199, 0.35) 73%,
      rgba(126, 156, 189, 0.35) 100%
    ),
    url(img/Aleksander-Victorian-City-hero-shot.jpg) no-repeat;
  background-size: cover;
  background-position: 0% 93%;
  -o-object-fit: fill;
  object-fit: fill;
  height: 100%;
  max-width: 100%;
  padding-top: 64px;
  padding-bottom: 112px;
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  -ms-flex-direction: row;
  flex-direction: row;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  clip-path: polygon(0 0, 100% 0, 100% 100%, 0 calc(100% - 5vw));
}

.testimonial .testimonial-container .testimonial-quote {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  -ms-flex-direction: row;
  flex-direction: row;
  -webkit-box-align: start;
  -ms-flex-align: start;
  align-items: flex-start;
}

.testimonial .testimonial-container .testimonial-author {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
}

.testimonial .testimonial-container.shira {
  background: -webkit-gradient(
      linear,
      left bottom,
      left top,
      from(rgba(61, 113, 172, 0.3)),
      color-stop(73%, rgba(93, 133, 199, 0.3)),
      to(rgba(126, 156, 189, 0.3))
    ),
    url(img/Shira-Eilan-Asha-hero-shot.jpg) no-repeat;
  background: linear-gradient(
      0deg,
      rgba(61, 113, 172, 0.35) 0%,
      rgba(93, 134, 199, 0.35) 73%,
      rgba(126, 156, 189, 0.35) 100%
    ),
    url(img/Shira-Eilan-Asha-hero-shot.jpg) no-repeat;
  background-position: 0% 15%;
  clip-path: polygon(0 0, 100% 3vw, 100% 100%, 0 100%);
}

.testimonial .testimonial-container.mikel {
  background: -webkit-gradient(
      linear,
      left bottom,
      left top,
      from(rgba(61, 113, 172, 0.3)),
      color-stop(73%, rgba(93, 133, 199, 0.3)),
      to(rgba(126, 156, 189, 0.3))
    ),
    url(img/Mikel-Ancient-Market-hero-shot.jpg) no-repeat;
  background: linear-gradient(
      0deg,
      rgba(61, 113, 172, 0.35) 0%,
      rgba(93, 134, 199, 0.35) 73%,
      rgba(126, 156, 189, 0.35) 100%
    ),
    url(img/Mikel-Ancient-Market-hero-shot.jpg) no-repeat;
  background-position: 0% 45%;
}

.testimonial img {
  border: 5px solid rgba(63, 107, 202, 0.466);
  border-radius: 50%;
}

.testimonial p {
  width: 50ch;
  display: inline-block;
  margin-left: 24px;
  line-height: 1.4;
  font-size: 1.618em;
  font-weight: 300;
  letter-spacing: 0.02em;
  font-family: "Inter", sans-serif;
}

.testimonial small {
  margin-top: 24px;
  text-align: center;
  font-family: "Inter", sans-serif;
  font-size: 1em;
  font-weight: 400;
}

.testimonial small span {
  display: block;
  font-size: 1em;
  font-weight: 700;
  margin-bottom: 12px;
}

.landing-page-nav {
  position: fixed;
  width: 100%;
  z-index: 9999;
  top: 0;
  left: 0;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
  box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 20px, rgba(0, 0, 0, 0.23) 0px 6px 6px;
}

.landing-page-nav .page-nav {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  -ms-flex-direction: row;
  flex-direction: row;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  max-height: auto;
  max-width: 100%;
  background-color: #333333;
  font-family: "Bebas Neue";
  letter-spacing: 0.2em;
  font-size: 1.618em;
}

.landing-page-nav .page-nav ul {
  list-style: none;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  -ms-flex-direction: row;
  flex-direction: row;
  -ms-flex-pack: distribute;
  justify-content: space-around;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
}

.landing-page-nav .page-nav ul li a {
  text-transform: uppercase;
  color: #ececec;
  text-decoration: none;
  margin: 0 1em;
  -webkit-transition: 0.3s ease;
  transition: 0.3s ease;
}

.landing-page-nav .page-nav ul li a:visited {
  color: #ececec;
}

.landing-page-nav .page-nav ul li a:hover {
  color: #d3d3d3;
  text-decoration: underline;
  text-underline-offset: 8px;
  -webkit-transition: 0.3s ease;
  transition: 0.3s ease;
}

.overview h2,
.features h2 {
  font-weight: bold;
  font-family: "Inter", sans-serif;
  font-size: 1.25em;
  text-transform: uppercase;
  margin-top: 2em;
  letter-spacing: 0.5px;
  color: #fff;
}

.overview span,
.features span {
  color: #3b71c2;
}

.overview {
  margin-top: 12.944em;
  max-height: auto;
  max-width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -ms-flex-line-pack: center;
  align-content: center;
}

.overview .overview-card-container {
  max-width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  color: #fff;
}

.overview .overview-card-container .overview-col-1 .overview-card:nth-child(2) {
  margin-top: 250px;
}

.overview .overview-card-container .overview-col-2 .overview-card:nth-child(1) {
  margin-top: 150px;
}

.overview .overview-card-container .overview-card {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  margin: 80px 95px;
}

.overview .overview-card-container .overview-card h3 {
  font-family: "Inter", sans-serif;
  font-size: 1.618em;
}

.overview .overview-card-container .overview-card p {
  font-family: "Noto Sans", sans-serif;
  font-size: 1em;
  line-height: 1.5;
  width: 50ch;
}

.overview .overview-card-container .overview-card p + p {
  margin-top: -8px;
}

.overview .overview-card-container .overview-card .img-wrapper {
  height: auto;
  width: 700px;
}

.overview .overview-card-container .overview-card .img-wrapper img {
  height: 100%;
  width: 100%;
  border-radius: 15px;
  transition: 0.3s ease-in-out;
}

.overview .overview-card-container .overview-card .img-wrapper img:hover {
  transform: scale(105%);
  box-shadow: rgba(0, 0, 0, 0.1) 0px 20px 25px -5px,
    rgba(0, 0, 0, 0.04) 0px 10px 10px -5px;
  transition: 0.3s ease-in-out;
}

.features {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 12.944em;
}

.features .features-title {
  text-align: center;
  margin-bottom: 8px;
}

.features h3 {
  font-family: "Inter", sans-serif;
  font-size: 1.618em;
  color: #fff;
  letter-spacing: 4px;
}

.features-slider {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
}

.features-slider .navigation {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  -ms-flex-direction: row;
  flex-direction: row;
}

.features-slider .navigation .btn {
  text-align: center;
  text-transform: uppercase;
  cursor: pointer;
  color: #ebebeb;
  background: none;
  font-size: 1.618em;
  padding: 8px 30px;
  display: inline;
  outline: 0;
  border: 0;
  margin: 0 24px;
  font-family: "Bebas Neue";
}

.features-slider .slider-wrapper {
  height: 50vh;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  align-items: center;
  justify-content: center;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  -ms-flex-direction: row;
  flex-direction: row;
}

.slider {
  position: relative;
  width: 85%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.slider .slide {
  z-index: 1;
  position: absolute;
  -webkit-transition: -webkit-clip-path 0.5s ease;
  transition: -webkit-clip-path 0.5s ease;
  transition: clip-path 0.5s ease;
  transition: clip-path 0.5s ease, -webkit-clip-path 0.5s ease;
  -webkit-clip-path: circle(0% at 0 50%);
  clip-path: circle(0% at 0 50%);
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: start;
  -ms-flex-align: start;
  align-items: flex-start;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  -ms-flex-direction: row;
  flex-direction: row;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
}

.slider .slide img {
  z-index: 1;
  width: 60%;
  margin-left: 485px;
  border-radius: 15px;
  box-shadow: rgba(0, 0, 0, 0.15) 0px 15px 25px,
    rgba(0, 0, 0, 0.05) 0px 5px 10px;
}

.slider .slide .info {
  position: absolute;
  margin-right: 48px;
}

.slider .slide .info p {
  font-family: "Noto Sans", sans-serif;
  font-size: 1.125em;
  width: 40ch;
  line-height: 1.7;
  font-size: 1.25em;
  color: #fff;
  line-height: 1.4;
}

.slider .slide .info span {
  font-family: "Inter", sans-serif;
  font-weight: 700;
  font-size: 0.925em;
}

.slider .slide.active {
  -webkit-clip-path: circle(150% at 0 50%);
  clip-path: circle(150% at 0 50%);
  -webkit-transition: -webkit-clip-path 2s ease;
  transition: -webkit-clip-path 2s ease;
  transition: clip-path 2s ease;
  transition: clip-path 2s ease, -webkit-clip-path 2s ease;
}

main .roadmap {
  display: flex;
  height: auto;
  padding: 24px 0;
  background-color: #131313;
  align-content: center;
  align-items: center;
  flex-direction: column;
}

main .roadmap .roadmap-container {
  display: flex;
  justify-content: space-evenly;
  align-items: flex-start;
  width: 60%;
}

main .roadmap .roadmap-container h2 {
  margin-bottom: 20px;
  font-weight: 200;
  font-size: 2.25em;
  letter-spacing: 0.15em;
}

main .roadmap .roadmap-container ul {
  list-style-position: inside;
  list-style: none;
}

main .roadmap .roadmap-container ul li {
  transition: 0.5s ease-out;
  border-radius: 3px;
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
}

main .roadmap .roadmap-container span {
  display: block;
  line-height: 1.7;
}

main .roadmap .roadmap-container .wip ul li {
  padding: 10px;
  border-left: 3px solid #1da1f2;
  margin-bottom: 30px;
  background-color: #2b2b2b;
}

main .roadmap .roadmap-container .planned ul li {
