.roadmap span {
  color: #3b71c2;
}

footer {
  font-family: "Inter", sans-serif;
  text-align: center;
  padding: 40px;
  font-size: 1.15em;
  background-color: #181818;
  color: #ebebeb;
  margin-top: 800px;
}

canvas {
  display: block;
}

#container {
  height: 70vh;
  width: 100vw;
}

@media screen and (max-width: 800px) {
  body {
    font-size: 1.618rem;
    overflow: hidden;
  }

  .landing-page-nav .page-nav {
    font-size: 1em;
  }

  .hero-container {
    max-width: 100%;
    height: 90vh;
  }

  .hero-container .title .logotype {
    font-size: 4.045em;
  }

  .hero-container .title .slogan {
    font-size: 0.925em;
  }

  .hero-container .title button.cta {
    font-size: 1em;
    padding: 15px 25px;
  }

  .testimonial {
    display: none;
  }

  .overview .overview-card-container .overview-card .img-wrapper {
    height: auto;
    width: 285px;
  }

  .overview .overview-card-container .overview-card {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    margin: 40px 35px;
  }

  .overview .overview-card-container .overview-card p {
    font-family: "Noto Sans", sans-serif;
    font-size: 1em;
    line-height: 1.5;
    width: 40ch;
  }

  @media screen and (max-width: 500px) {
    body {
      font-size: 0.975em;
    }
  }
}
